import React, { PureComponent } from "react"; 
import { NO_PRODUCT } from "../../../../constants";
import { GET_MEDIA_NAME } from "../../../../utils/Common";
export default class ProductImage extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            loadError: false,
            paddingBottom: 100,
            imgUrl: this.props.media
        }
    }

    onLoad = event => event.target.getAttribute("data-load") == 1 && this.setState({ imgUrl: event.target.src, loadError: false })
     
    UNSAFE_componentWillReceiveProps(nextProps) {
        const { itemDetails } = nextProps

        const { loadError } = this.state
        if (loadError && nextProps != this.props) {
            if (itemDetails && Object.keys(itemDetails).length && itemDetails.showCart) this.setState({ imgUrl: itemDetails.data.featured_image, loadError: false })
        }
    }

    render() {
        const { ImageClass } = this.props
        const { imgUrl, paddingBottom } = this.state

        const imageStyle = {
            paddingBottom: `${paddingBottom}%`
        };

        return <div className={`${ImageClass}_wrap`} style={imageStyle}>
            <img
                className={ImageClass}
                role="img"
                src={imgUrl}
                //data-src={imgUrl}
                height={300}
                width={300}
                onLoad={this.onLoad}
                onError={(e) => {
                    this.setState({ loadError: true })
                    e.target.src = NO_PRODUCT
                }}
                alt={GET_MEDIA_NAME(imgUrl)}
            />
        </div>
    }
}