import React from 'react';
import ProductImage from '../../../Components/PostPopUp/Elements/ProductImage';
import ReactHtmlParser from 'react-html-parser';
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from 'swiper/modules';
import 'swiper/css';

const CalculateDiscount = (originalPrice, discountedPrice) => {
       if (originalPrice != 0) {
              if (discountedPrice > 0 && discountedPrice < originalPrice) {
                     let discountAmount = originalPrice - discountedPrice;
                     let discountPercentage = (discountAmount / originalPrice) * 100;
                     return discountPercentage > 0 ? `${discountPercentage.toFixed()}%` : '';

              } else {
                     return false
              }
       } else {
              return false
       }
}
const Products = ({ Post, ProductSetting }) => {
       const products = Post.products
       const { Price, ProductTitle } = ProductSetting
       const isMultipleProduct = products && products.length == 1 ? true : false
       return <div className="tb_rtp_wrap" id={`slide-${Post.id}`}>
              <div className="tb_rtp_wrap_in">
                     <Swiper
                            id={`product_slide-${Post.id}`}
                            modules={[Pagination, Navigation]}
                            spaceBetween={10}
                            navigation={{
                                   prevEl: `#tb_rtn_prev-${Post.id}`,
                                   nextEl: `#tb_rtn_next-${Post.id}`,
                            }}
                     >
                            {products.map((product, index) => <SwiperSlide key={index}><div className='tb_rtp' key={index}>
                                   <div className='tb_rtp_media' style={{ opacity: product.image ? 1 : 0 }}>
                                          <ProductImage ImageClass="tb_rtp_img" media={product.image} size={false} itemDetails={''} />
                                   </div>
                                   <div className='tb_rtp_title_w'>
                                          {(product.title) ?
                                                 <div className={`tb_rtp_title tb-cTBfont-${ProductTitle.varient}`}>{ReactHtmlParser(product.title)}</div>
                                                 : null}
                                   </div>
                                   {product.price > 0 || product.discount > 0 ?
                                          <div className="tb_rtp_price_wrap">
                                                 <div className={`tb_rtp_price tb-cTBfont-${Price.varient}`}>
                                                        {product.currency}{product.discount > 0 ? product.discount : product.price}
                                                 </div>
                                                 {product.discount > 0 && product.discount != product.price ? <div className={`tb_rtp_d_price tb-cTBfont-${Price.varient}`}>{product.currency}{product.price}</div> : null}


                                                 {CalculateDiscount(Number(product.price), Number(product.discount)) && CalculateDiscount(Number(product.price), Number(product.discount)) != '' ? <div className='tb_rtp_dp'>{CalculateDiscount(Number(product.price), Number(product.discount))}</div> : null}
                                          </div>
                                          : null}
                            </div>
                            </SwiperSlide>)}
                     </Swiper>
                     {!isMultipleProduct ?
                            <div className={`tb_rtn_wrap`} onClick={(e) => { e.stopPropagation() }}>
                                   <div id={`tb_rtn_prev-${Post.id}`} className={`tb_rtn_btn tb_rtn_btn_left tb__icon tb-arrow-left-alt`}><div></div></div>
                                   <div id={`tb_rtn_next-${Post.id}`} className={`tb_rtn_btn tb_rtn_btn_right tb__icon tb-arrow-right-alt`}><div></div></div>
                            </div>
                            : null}
              </div>
       </div>
}
export default Products;